/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import ReactMapGL, { NavigationControl, Source, Layer } from 'react-map-gl'
import ZoomOutMapIcon from '@mui/icons-material/ZoomOutMap'
import 'mapbox-gl/dist/mapbox-gl.css'

import { mapStyles, MapStyleKey, sitesLayer } from './styles'

import { SitesMapController, SITES_LAYER_ID } from './useSitesMapController'
import { atom, useRecoilState } from 'recoil'
import { StyleControl } from './StyleControl'

export interface SitesMapProps {
  controller: SitesMapController
  minimal?: boolean
  children?: React.ReactNode
}

const { REACT_APP_MAPBOX_TOKEN } = process.env

export const MAX_ZOOM = 22

export const mapStyleKeyAtom = atom<MapStyleKey>({
  key: 'SitesMap/mapStyleKeyAtom',
  default: 'basic',
})

export const SitesMap = ({ controller, children, minimal }: SitesMapProps): JSX.Element => {
  const { viewport, setViewport, sitesData, handleClick, handleHover, hoverInfo, smartFitAll } = controller
  const [mapStyleKey, setMapStyleKey] = useRecoilState(mapStyleKeyAtom)

  // MAP STYLE

  const [styleChange, setStyleChange] = useState(0)
  const onMapStyleKeyChange = (updatedMapStyleKey: MapStyleKey) => {
    setStyleChange((v) => v + 1) // increment to force redraw of all sources and layers by updating array key; can't use the actual mapStyleKey for that (throws error)
    setMapStyleKey(updatedMapStyleKey)
  }

  const mapStyle = mapStyles[mapStyleKey]

  // RENDER

  if (!REACT_APP_MAPBOX_TOKEN)
    return (
      <div>
        No Mapbox token found. Add <code>REACT_APP_MAPBOX_TOKEN</code> to relevant <code>.env</code> file.
      </div>
    )

  return (
    <div css={{ height: '100%', width: '100%' }}>
      <ReactMapGL
        onViewportChange={setViewport}
        mapboxApiAccessToken={REACT_APP_MAPBOX_TOKEN}
        onHover={handleHover}
        interactiveLayerIds={[SITES_LAYER_ID]}
        onClick={handleClick}
        maxZoom={MAX_ZOOM}
        {...viewport} // must be before width and height
        width="100%"
        height="100%"
      >
        <NavigationControl
          style={{
            right: 10,
            top: 10,
          }}
          showCompass={false}
        />
        <div css={{ position: 'absolute', top: 80, right: 10 }}>
          <div className="mapboxgl-ctrl mapboxgl-ctrl-group">
            <button className="mapboxgl-ctrl-icon" type="button" title="Zoom to Fit" onClick={smartFitAll}>
              <ZoomOutMapIcon sx={{ height: 18, width: 18, paddingTop: '4px' }} />
            </button>
          </div>
        </div>

        {[
          <React.Fragment key={styleChange}>
            {mapStyle.sources.map((source) => (
              <Source key={source.id} {...source}>
                {mapStyle.layers
                  .filter((layer) => layer.source === source.id)
                  .map((layer) => (
                    <Layer key={layer.id} {...layer} />
                  ))}
              </Source>
            ))}
            <Source id={SITES_LAYER_ID} type="geojson" data={sitesData}>
              <Layer {...sitesLayer} />
            </Source>
          </React.Fragment>,
        ]}
        {children}
        {hoverInfo && (
          <div
            css={{
              position: 'absolute',
              left: hoverInfo.x,
              top: hoverInfo.y - 10,
              pointerEvents: 'none',
              transform: 'translate(-50%,-100%)',
            }}
          >
            <div>{hoverInfo.node}</div>
          </div>
        )}
      </ReactMapGL>
      {!minimal && (
        <div css={{ position: 'absolute', bottom: 30, left: 8 }}>
          <StyleControl value={mapStyleKey} onChange={onMapStyleKeyChange} />
        </div>
      )}
    </div>
  )
}
