import { LayerProps, SourceProps } from 'react-map-gl'

import basicStylesJson from './basicStyles.json'
import satStylesJson from './satStyles.json'
import topoStylesJson from './topoStyles.json'
import sitesLayerJson from './sites.json'

import basicThumb from './basicThumb.png'
import satThumb from './satThumb.jpg'
import topoThumb from './topoThumb.png'

export const mapStyles = {
  basic: {
    sources: Object.entries(basicStylesJson.sources).map(([id, properties]) => ({
      id,
      ...properties,
    })) as SourceProps[],
    layers: basicStylesJson.layers as LayerProps[],
  },
  sat: {
    sources: Object.entries(satStylesJson.sources).map(([id, properties]) => ({ id, ...properties })) as SourceProps[],
    layers: satStylesJson.layers as LayerProps[],
  },
  topo: {
    sources: Object.entries(topoStylesJson.sources).map(([id, properties]) => ({ id, ...properties })) as SourceProps[],
    layers: topoStylesJson.layers as LayerProps[],
  },
}

export const mapStylesThumbs = {
  basic: basicThumb,
  sat: satThumb,
  topo: topoThumb,
}

export const sitesLayer = sitesLayerJson as LayerProps

export type MapStyleKey = keyof typeof mapStyles
